/* lekton-regular - latin */
@font-face {
    font-family: 'Lekton';
    font-style: normal;
    font-weight: 400;
    src: local(''),
         url('/assets/fonts/lekton-v17-latin-regular.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
         url('/assets/fonts/lekton-v17-latin-regular.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  }
  /* lekton-italic - latin */
