@import './font.css';
@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

[x-cloak] {
    visibility: hidden;
}
.card-title {
    @apply font-display;
}

.text-ig {
    background: linear-gradient(220deg, #231557 0%, rgba(68, 16, 122, 0.645833) 34.9%, #FF1361 68.75%, #FFF800 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

@media (prefers-color-scheme: dark) {
    .text-ig {
        background: linear-gradient(220deg, #c6b7fe 0%, rgb(182 112 255 / 65%) 34.9%, #FF1361 68.75%, #FFF800 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
    }
}

[data-theme="dark"] .text-ig {
    background: linear-gradient(220deg, #c6b7fe 0%, rgb(182 112 255 / 65%) 34.9%, #FF1361 68.75%, #FFF800 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}
[class*="prose"] table{
    @apply table w-full;
}
[class*="prose"] table th{
    text-align: inherit;
}

[class*="prose"] ol li{
    list-style-type: auto;
}

[class*="prose"] ul li{
    list-style-type: "⦁";
}

[class*="prose"] ul li:hover{
    list-style-type: "→";
}

[class*="prose"] a{
    @apply link link-hover link-primary;
}
